import * as React from 'react';

const OpenVSXLogo: React.FunctionComponent<{ className: string, prefersDarkMode: boolean }> = props => {
    const fontColor = props.prefersDarkMode ? '#fff' : '#000';
    return (
            <div style={{ textAlign:'center' }}>
                <img width='36' height='48' style={{ paddingTop:'8px' }} src='data:image/png;base64,'/>
                <p style={{ color:fontColor, fontSize:'32px', fontFamily:'monospace', display:'inline-block', marginLeft:'10px', marginBottom:'5px', marginTop:'0', verticalAlign:'bottom' }}>SmartIDE Marketplace(基于Eclipse OpenVSX)</p>
            </div>
    );
};

export default OpenVSXLogo;
